						const timeA = new Date(a.lastTime);
						const timeB = new Date(b.lastTime);
						return timeB - timeA; // ÈôçÂ∫èÊéíÂàóÔºåÊúÄÊñ∞ÁöÑÂú®ÂâçÈù¢
					});
				} else {
					// Â¶ÇÊûúÊ≤°ÊúâÂ≠òÂÇ®ÁöÑÊï∞ÊçÆÔºå‰ΩøÁî®Ê®°ÊãüÊï∞ÊçÆ‰Ωú‰∏∫Á§∫‰æã
					this.historyChats = [
						{
							id: 'chat1',
							title: 'ÂÖ≥‰∫éÁéâÁ±≥ÂÆ≥Ëô´ÁöÑÂí®ËØ¢',
							lastTime: '2024-01-15 14:30',
							messages: [
								{
									type: 'user',
									content: 'ÁéâÁ±≥Âú∞ÈáåÂèëÁé∞‰∫ÜËô´Â≠êÔºåËÉΩÂ∏ÆÊàëËØÜÂà´‰∏Ä‰∏ãÂêóÔºü',
									time: '14:30'
								},
								{
									type: 'ai',
									content: 'Ê†πÊçÆÊÇ®ÁöÑÊèèËø∞ÔºåËøôÂèØËÉΩÊòØÁéâÁ±≥Ëûü„ÄÇÂª∫ËÆÆÊÇ®‰∏ä‰º†ÂõæÁâáËøõË°åÊõ¥ÂáÜÁ°ÆÁöÑËØÜÂà´„ÄÇ',
									time: '14:31'
								}
							]
						},
						{
							id: 'chat2',
							title: 'Ê∞¥Á®ªÁóÖËô´ÂÆ≥Èò≤Ê≤ª',
							lastTime: '2024-01-14 09:15',
							messages: [
								{
									type: 'user',
									content: 'Ê∞¥Á®ªÂè∂Â≠êÂèëÈªÑÔºåÊòØ‰ªÄ‰πàÂéüÂõ†Ôºü',
									time: '09:15'
								},
								{
									type: 'ai',
									content: 'Ê∞¥Á®ªÂè∂Â≠êÂèëÈªÑÂèØËÉΩÊòØÁº∫ËÇ•ÊàñÁóÖËô´ÂÆ≥ÂØºËá¥ÁöÑ„ÄÇÂª∫ËÆÆÊÇ®Ê£ÄÊü•ÊòØÂê¶ÊúâËô´ÂÆ≥ÔºåÂπ∂ÈÄÇÂΩìÊñΩËÇ•„ÄÇ',
									time: '09:16'
								}
							]
						},
						{
							id: 'chat3',
							title: 'Ëî¨ËèúÂÆ≥Ëô´ËØÜÂà´',
							lastTime: '2024-01-13 16:45',
							messages: [
								{
									type: 'user',
									content: 'ÁôΩËèú‰∏äÊúâÂ∞èËô´Â≠êÔºåÊÄé‰πàÂ§ÑÁêÜÔºü',
									time: '16:45'
								},
								{
									type: 'ai',
									content: 'ËøôÂèØËÉΩÊòØËèúÈùíËô´„ÄÇÂª∫ËÆÆ‰ΩøÁî®ÁîüÁâ©ÂÜúËçØÊàñ‰∫∫Â∑•ÊçïÊùÄÔºåÈÅøÂÖç‰ΩøÁî®È´òÊØíÂÜúËçØ„ÄÇ',
									time: '16:46'
								}
							]
						},
						{
							id: 'chat4',
							title: 'ÊûúÊ†ëÁóÖËô´ÂÆ≥Âí®ËØ¢',
							lastTime: '2024-01-12 11:20',
							messages: [
								{
									type: 'user',
									content: 'ËãπÊûúÊ†ë‰∏äÊúâËöúËô´ÔºåÁî®‰ªÄ‰πàËçØÊØîËæÉÂ•ΩÔºü',
									time: '11:20'
								},
								{
									type: 'ai',
									content: 'ÂØπ‰∫éËãπÊûúÊ†ëËöúËô´ÔºåÂª∫ËÆÆ‰ΩøÁî®Âê°Ëô´ÂïâÊàñÂï∂Ëô´ËÑíÁ≠âËçØÂâÇÔºåÊ≥®ÊÑèËΩÆÊç¢‰ΩøÁî®ÈÅøÂÖçÊäóÊÄß„ÄÇ',
									time: '11:21'
								}
							]
						},
						{
							id: 'chat5',
							title: 'Â∞èÈ∫¶ÁóÖËô´ÂÆ≥Èò≤Ê≤ª',
							lastTime: '2024-01-11 15:30',
							messages: [
								{
									type: 'user',
									content: 'Â∞èÈ∫¶Âè∂Â≠êÊúâÁôΩÁ≤âÁóÖÔºåÊÄé‰πàÈò≤Ê≤ªÔºü',
									time: '15:30'
								},
								{
									type: 'ai',
									content: 'Â∞èÈ∫¶ÁôΩÁ≤âÁóÖÂèØ‰ª•‰ΩøÁî®‰∏âÂîëÈÖÆ„ÄÅÊàäÂîëÈÜáÁ≠âËçØÂâÇÈò≤Ê≤ªÔºåÂêåÊó∂Ê≥®ÊÑèÈÄöÈ£éÈÄèÂÖâ„ÄÇ',
