				});
				
				// ‰ΩøÁî®Êõ¥ÁÆÄÂçïÁöÑË∑≥ËΩ¨ÊñπÂºè
				setTimeout(() => {
					// Â∞ùËØï‰ΩøÁî®switchTabÔºåÂõ†‰∏∫chatAIÊòØtabBarÈ°µÈù¢
					uni.switchTab({
						url: '/pages/chatAI/chatAI',
						success: () => {
							console.log('switchTabË∑≥ËΩ¨ÊàêÂäü');
						},
						fail: (err) => {
							console.log('switchTabÂ§±Ë¥•ÔºåÂ∞ùËØïnavigateTo:', err);
							// Â¶ÇÊûúswitchTabÂ§±Ë¥•ÔºåÂ∞ùËØïnavigateTo
