								success: () => {
									console.log('navigateToË∑≥ËΩ¨ÊàêÂäü');
								},
								fail: (navigateErr) => {
									console.log('navigateTo‰πüÂ§±Ë¥•:', navigateErr);
									uni.showToast({
										title: 'Ë∑≥ËΩ¨Â§±Ë¥•ÔºåËØ∑ÊâãÂä®ËøîÂõû',
										icon: 'none',
										duration: 2000
									});
								}
							});
						}
					});
				}, 100);
			},
			
			// Âà†Èô§ÂØπËØù
			deleteChat(chatId) {
				uni.showModal({
					title: 'Âà†Èô§ÂØπËØù',
					content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™ÂØπËØùÂêóÔºüÂà†Èô§ÂêéÊó†Ê≥ïÊÅ¢Â§ç„ÄÇ',
					success: (res) => {
						if (res.confirm) {
							this.historyChats = this.historyChats.filter(chat => chat.id !== chatId);
							// Êõ¥Êñ∞Êú¨Âú∞Â≠òÂÇ®
							uni.setStorageSync('historyChats', this.historyChats);
							uni.showToast({
								title: 'Âà†Èô§ÊàêÂäü',
