			},
			
			// Ê∏ÖÁ©∫ÊâÄÊúâÂéÜÂè≤
			clearAllHistory() {
				uni.showModal({
					title: 'Ê∏ÖÁ©∫ÂéÜÂè≤',
					content: 'Á°ÆÂÆöË¶ÅÊ∏ÖÁ©∫ÊâÄÊúâÂéÜÂè≤ÂØπËØùÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§ç„ÄÇ',
					success: (res) => {
						if (res.confirm) {
							this.historyChats = [];
							// Ê∏ÖÁ©∫Êú¨Âú∞Â≠òÂÇ®
							uni.removeStorageSync('historyChats');
							uni.showToast({
								title: 'Â∑≤Ê∏ÖÁ©∫ÊâÄÊúâÂéÜÂè≤',
								icon: 'success'
							});
						}
					}
				});
			},
			
			// ÊêúÁ¥¢ËæìÂÖ•Â§ÑÁêÜ
			onSearchInput() {
				// ÂÆûÊó∂ÊêúÁ¥¢Ôºå‰∏çÈúÄË¶ÅÈ¢ùÂ§ñÂ§ÑÁêÜ
			},
			
			// ÊâßË°åÊêúÁ¥¢
			performSearch() {
				if (this.searchKeyword.trim()) {
					const count = this.filteredHistoryChats.length;
					if (count > 0) {
						uni.showToast({
							title: `ÊâæÂà∞ ${count} ‰∏™ÂØπËØù`,
							icon: 'none'
						});
					} else {
						uni.showToast({
							title: 'Êú™ÊâæÂà∞Áõ∏ÂÖ≥ÂØπËØù',
							icon: 'none'
						});
					}
				}
			},
			
			// Ê∏ÖÈô§ÊêúÁ¥¢
			clearSearch() {
				this.searchKeyword = '';
			},
			
			// È´ò‰∫ÆÊêúÁ¥¢ÊñáÊú¨ÔºàÂ∑≤ÁßªÈô§ÔºåÊîπÁî®CSSÊ†∑ÂºèÂÆûÁé∞Ôºâ
			highlightSearchText(text) {
				return text; // Áõ¥Êé•ËøîÂõûÂéüÊñáÊú¨Ôºå‰ΩøÁî®CSSÊ†∑ÂºèÂÆûÁé∞È´ò‰∫Æ
			},
			
			// Ê£ÄÊü•ÂØπËØùÊòØÂê¶È´ò‰∫Æ
			isChatHighlighted(chat) {
				if (!this.searchKeyword.trim()) return false;
				
				const keyword = this.searchKeyword.toLowerCase();
				// Ê£ÄÊü•Ê†áÈ¢ò
				if (chat.title && chat.title.toLowerCase().includes(keyword)) {
					return true;
				}
				// Ê£ÄÊü•Ê∂àÊÅØÂÜÖÂÆπ
				if (chat.messages) {
					return chat.messages.some(message => 
						message.content && message.content.toLowerCase().includes(keyword)
					);
				}
				return false;
			}
		}
	}
</script>

